body {
  background: lightgrey;
  text-align: center;
}

.metronome {
  text-align: center;
  max-width: 375px;
  margin: 0 auto;
  padding: 30px;
}

.metronome button {
  background: #C94D46;
  padding: 10px;
  border: 1px solid #832420;
  border-radius: 2px;
  width: 100px;
  color: #fff;
  font-size: 18px;
}

.navbar {
  border-radius: 0;
}

.slider input {
  width: 100%;
  margin: 10px;
}



